// { fecha: string }[]
export function obtenerRangoFechas(datos: any) {
  let minTime = Infinity;
  let maxTime = -Infinity;

  for (const item of datos) {
    const time = new Date(item.fecha).getTime();
    if (time < minTime) minTime = time;
    if (time > maxTime) maxTime = time;
  }

  return {
    inicio: new Date(minTime).toISOString(),
    fin: new Date(maxTime).toISOString()
  };
}

export function completarIntervalosTempVolt(
  datos: any[],
  inicio: string,
  fin: string,
  intervaloMin = 15
) {
  const resultado: { fecha: string; valores: string }[] = [];

  const mapDatos = new Map<number, string>(
    datos.map(d => [new Date(d.fecha).getTime(), d.valores])
  );

  const intervaloMs = intervaloMin * 60 * 1000;
  const inicioTime = new Date(inicio).getTime();
  const finTime = new Date(fin).getTime();

  for (let t = inicioTime; t <= finTime; t += intervaloMs) {
    const valores = mapDatos.has(t) ? mapDatos.get(t)! : `{"temperature": [0], "voltage": [0]}`;
    resultado.push({
      fecha: new Date(t).toISOString(),
      valores
    });
  }

  return resultado;
}

export function completarIntervalosInstamesh(
  datos: any[],
  inicio: string,
  fin: string,
  intervaloMin = 15
) {
  const resultado: { fecha: string; wired: string, wireless: string }[] = [];

  const mapDatos = new Map<number, any>(
    datos.map(d => [new Date(d.fecha).getTime(), d])
  );

  const intervaloMs = intervaloMin * 60 * 1000;
  const inicioTime = new Date(inicio).getTime();
  const finTime = new Date(fin).getTime();
  let defaultWireless = ''

  for (let t = inicioTime; t <= finTime; t += intervaloMs) {
    const valuesFind = mapDatos.get(t);
    if(t === inicioTime) defaultWireless = getWirelessDefault(valuesFind)
    resultado.push({
      fecha: new Date(t).toISOString(),
      wired: valuesFind ? valuesFind.wired :  `{"eth0": {"rxBytes": 0, "txBytes": 0}, "cost": 0}` ,
      wireless: valuesFind ? valuesFind.wireless : defaultWireless
    });
  }

  return resultado;
}


function getWirelessDefault (value:any){

    let finalString = ''
    const finalITEM = JSON.parse(value.wireless)

    finalITEM.wlan0 ? finalString = '{"wlan0": { "noise": 0, "rxBytes": 0, "txBytes": 0 }, "cost": 0}' : null
    finalITEM.wlan1 ? finalString = '{"wlan0": { "noise": 0, "rxBytes": 0, "txBytes": 0 }, "wlan1": { "rxBytes": 0, "txBytes": 0 }, "cost": 0}' : null
    finalITEM.wlan2 ? finalString = '{"wlan0": { "noise": 0, "rxBytes": 0, "txBytes": 0 }, "wlan1": { "rxBytes": 0, "txBytes": 0 }, "wlan2": { "rxBytes": 0, "txBytes": 0 }, "cost": 0}' : null
    finalITEM.wlan3 ? finalString = '{"wlan0": { "noise": 0, "rxBytes": 0, "txBytes": 0 }, "wlan1": { "rxBytes": 0, "txBytes": 0 }, "wlan2": { "rxBytes": 0, "txBytes": 0 }, "wlan3": { "rxBytes": 0, "txBytes": 0 }, "cost": 0}' : null


    return finalString
}


